img {
  // style for img in the pillar
  height: auto;
  padding-left: 0 !important;
  padding-right: 0 !important;
  width: 100% - ($heaven-width + $hell-width);

  @include tight_and_cosy {
    margin-left: $god-space !important;
    margin-right: $god-space !important;
    width: 100% - ($god-space * 2) !important;
  }
}
