/// innocent_menus mixin
//
/// @param {UNIT} $mp Menu padding
/// @param {UNIT} $ih Image height
//
//
/// @example
/// menu {
///   @include innocent_menus();
/// }
/// nav {
///   @include innocent_menus(12px, 48px);
/// }
@mixin innocent_menus($mp: 0, $ih: 0) {
  box-sizing: border-box;

  * {
    box-sizing: border-box;
  }

  &:after {
    clear: both;
    content: '';
    display: block;
    height: 0;
  }

  a {
    text-decoration: none;
  }

  label {
    font-weight: bold;
  }

  a,
  label {
    cursor: pointer;
    display: block;
    @if not no_means_no($mp) {
      padding: $mp;
    }
    white-space: nowrap;
    img {
      @if not no_means_no($ih) {
        height: $ih;
        width: auto;
      }
    }
  }

  menu,
  nav {
    margin: 0;
    padding: 0;
  }

  menu > nav menu nav {
    @if not no_means_no($mp) {
      padding-left: $mp;
    }
  }
  // Hidden
  menu > nav {
    background: white;
    max-height: 0;
    opacity: 0;
    visibility: hidden;
    overflow: hidden;
  }
  // Visible
  menu:hover > nav {
    max-height: 500px;
    opacity: 1;
    visibility: visible;
  }
}
