/* god theme: Call me to include god in your SASS projects.*/
@import './node_modules/sassy-lists/dist/SassyLists';
@import './node_modules/@elioway/sassy-fibonacciness/dist/SassyFibonacciness';

@import 'functions/no_means_no';
@import 'functions/strip_unit';
@import 'mixins';

$skiponacci: skiponacci(6);
$fontonacci: fontonacci($skiponacci, $god-h6, $god-h1);
$container: sl-to-string($container, '>,');
$heaven: sl-to-list($heaven, comma);
$hell: sl-to-list($hell, comma);

#{$container} {
  @include heading_sizes($fontonacci);
  @import 'pillar';
  @import 'heaven';
  @import 'hell';
  @import 'sinner';
  @import 'ol/pillar';
  @import 'ul/pillar';
}
