// LAYOUT ==================
$god-space: 2%; // the horizontal padding in all the prongs.
$heaven-width: 10%; // prong width right (as a percentage to help scaling).
$hell-width: 30%; // prong width left (as a percentage to help scaling).
$personal-space: 18px; // the vertical padding between believers.
$transition: 1s;
// MEDIA BREAKS ==================
$god-loose: 1300px; // loose - prongs divided - no overlap.
$god-cosy: 1000px; // cosy - overlap between sinners and the pillar.
$god-tight: 800px; // tight - believers full width - piller width small margin.
// COLORS ==================
$heaven-color: black;
$pillar-color: black;
$hell-color: #ead1dc;
$god-color: $pillar-color;
$god-blushed: lighten($heaven-color, 65);
// TEXT ==================
$god-h1: 2vw; // font size of h1.
$god-h6: 1.2vw; // font size of h6.
$god-p: 100%;  // font size of paragraph text.
$god-spoke: $god-p * 1.3; // standout with big text.
$god-head: 'Montserrat'; // for heading level text.
$god-body: $god-head; // for body level text.
// BASICS ==================
$god-border: 1px $god-blushed solid; // general purpose border
$inline-padding: 3px;
$toothpaste-color: 'none';
// BELIEVERS ==================
$container: (body, main); // sin classes only affect direct children of these
$pillar: (
  h1,
  h2,
  h3,
  h4,
  p,
  ol,
  ul,
  dl,
  hr,
  footer,
  pre
); // pillar - all tags are stictly contained the same way - no believers here.
$heaven: (div); // hangs right - gets the best views.
$hell: (blockquote, header, menu, nav, figure); // hangs left.

$god-border-thickness: 1px;
$inline-padding: 3px;
@import 'blockquote/settings';
