figure {
  border: $god-border;
  box-sizing: border-box;
  padding: $god-space !important;
  img {
    box-sizing: border-box;
    height: auto;
    width: 100%;
  }
  figcaption {
    @include fontize_god($god-head, 'none', $pillar-color);
    margin-top: $personal-space;
  }

  @include tight {
    margin-left: $god-space !important;
    margin-right: $god-space !important;
    width: 100% - ($god-space * 2) !important;
  }
}
